import React, { Component } from 'react';

class Timer extends React.Component {
    constructor(props) {
      super(props);
      this.state = { 
        time: {}, 
        seconds: props.time,
        //time_stop: false 
      };
      this.startTimer = this.startTimer.bind(this);
      this.stopTimer = this.stopTimer.bind(this);
      this.countDown = this.countDown.bind(this);
      this.timer = 0;
      this.temp_timer = 0;
    }
  
    secondsToTime(secs){
      let hours = Math.floor(secs / (60 * 60));
  
      let divisor_for_minutes = secs % (60 * 60);
      let minutes = Math.floor(divisor_for_minutes / 60);
  
      let divisor_for_seconds = divisor_for_minutes % 60;
      let seconds = Math.ceil(divisor_for_seconds);
  
      let obj = {
        "h": hours,
        "m": minutes,
        "s": seconds
      };
      return obj;
    }
  
    componentDidMount() {
      let timeLeftVar = this.secondsToTime(this.state.seconds);
      this.setState({ time: timeLeftVar });
    }
  
    startTimer() {
      if (this.timer == 0 && this.state.seconds > 0) {
        this.timer = setInterval(this.countDown, 1000);
      }
      if(this.state.time_stop === true){
        this.timer = setInterval(this.countDown, 1000);
        this.setState({
          time_stop:false
        });
      }
    }

    stopTimer(){
      /*this.setState({
        seconds: this.timer
      });*/
      this.props.setTimer(this.timer);
      this.temp_timer = this.timer;
      this.setState({
        time_stop:true
      });
      //this.props.setState({
      //    timer:this.timer
      //});
      clearInterval(this.timer);
    }
  
    countDown() {
      // Remove one second, set state so a re-render happens.
      let seconds = this.state.seconds - 1;
      this.setState({
        time: this.secondsToTime(seconds),
        seconds: seconds,
      });
      
      // Check if we're at zero.
      if (seconds == 0) { 
        clearInterval(this.timer);
      }
    }
  
   /*componentDidUpdate(prevProps, prevState) {
      if(this.props.time_start){
        this.startTimer();
      }
      if(this.props.time_stop){
        this.stopTimer();
      }
    }*/

    render() {
      const divstyle = {
        marginLeft: '-100px'
      }
      return(
        <div style={divstyle}>
          <strong>{this.state.time.m}:{this.state.time.s<10?'0'+this.state.time.s:this.state.time.s}</strong>
        </div>
      );
    }
  }

export default Timer;